.question {
    position: relative;
    background-color: #E9EBED;
    text-align: center;
    overflow: hidden;
}

.questionContent {
    padding: 77px 0 78px;
    display: inline-block;
    text-align: center;
    max-width: 751px;
    z-index: 5;
}

.container {
    z-index: 5;
}

.questionTitle {
    display: inline-block;
    font-family: 'GothamProMedium';
    font-style: normal;
    font-weight: 500;
    font-size: 40px;
    line-height: 110%;
    letter-spacing: -0.02em;
    color: var(--rock-grey-color);
}

.questionDescr {
    margin-top: 27px;
    margin-bottom: 38px;
    font-family: 'GothamProRegular';
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 150%;
    text-align: center;
    letter-spacing: -0.02em;
    color: #6074835f;
}

.questionDescrForm {
    margin-top: 20px;
    font-family: 'GothamProRegular';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 130%;
    text-align: center;
    letter-spacing: -0.02em;
    color: #6074837f;
}

.questionBtn {
    margin-top: 34px;
    padding: 28px 127px;
    font-family: 'GothamProMedium';
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 11px;
    text-align: center;
    text-transform: uppercase;
    color: #FFFFFF;
}

.questionFormTop {
    display: grid;
    grid-template-columns: repeat(2, auto);
    gap: 20px;
    width: 100%;
}

.questionInput {
    height: 100%;
    padding: 24px 37px 18px;
    border: none;
    overflow: hidden; 
    background: #fff;
    color: #ACB8C0;
}


.questionInputP {
font-family: 'GothamProRegular';
font-style: normal;
font-weight: 400;
font-size: 14px;
line-height: 130%;
/* or 18px */

letter-spacing: -0.02em;

color: #ACB8C0;
}


.questionInput:valid:required ~  .questionInputP,
.questionInput:focus ~ .questionInputP {


    color: #ACB8C0;
}


.questionForm {
    box-sizing: border-box;
}

.questionMask {
    padding: 24px 37px 24px;
    overflow: hidden; 
    border: none;
    font-family: 'GothamProRegular';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 130%;
    letter-spacing: -0.02em;
    color: #ACB8C0;
    background: #fff;
}

.questionMask::placeholder {
    font-family: 'GothamProRegular';
    font-style: normal;
    color: #ACB8C0;
    font-weight: 400;
    font-size: 14px;
    line-height: 130%;
}


::-webkit-input-placeholder, ::-webkit-input-placeholder, ::-moz-placeholder, :-moz-placeholder, :-ms-input-placeholder  {
    font-family: 'GothamProRegular';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 130%;
    letter-spacing: -0.02em;
    color: #ACB8C0;
}

.questiontextAreaBlock {
    margin-top: 15px;
    min-height: 166px;
    color: #ACB8C0;
}

.questionTextarea {
    padding: 23px 37px;
    resize: none;
    height: 100%;
    min-height: 166px;
    background-color: #fff;
    border-radius: 5px;
    font-family: 'GothamProRegular';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 130%;
    letter-spacing: -0.02em;
    color: #ACB8C0;
    outline: none;
}

.questionTextareaP {
    font-family: 'GothamProRegular';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 130%;
    letter-spacing: -0.02em;
    color: #ACB8C0;
}

.questionLeftImg {
    position: absolute;
    width: 598px;
    height: 598px;
    background-image: url('../assets/img/grey-item-bg-left.svg');
    background-repeat: no-repeat;
    background-size: contain;
    left: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    z-index: 0;
    pointer-events: none;
}

.questionRightImg {
    position: absolute;
    width: 349px;
    height: 475px;
    background-image: url('../assets/img/grey-bg-right-itemsvg.svg');
    background-position: left;
    background-repeat: no-repeat;
    background-size: contain;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    z-index: 0;
    pointer-events: none;
}


.modalTitle {
    margin-bottom: 32px;
    font-family: 'GothamProMedium';
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 110%;
    text-align: center;
    letter-spacing: -0.02em;
    color: #25495C;
}

.modalDescr {
    display: block;
    font-family: 'GothamProRegular';
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 150%;
    text-align: center;
    letter-spacing: -0.02em;
    padding:  0 10px;
    color: #607483;
    opacity: 0.8;
    margin-bottom: 12px;
} 

