.MainDev {
    margin-top: 138px;
    min-height: 350px;
    height: auto;
    overflow: hidden;
}

.container {
    height: 100%;

}

.wrapper {
    /* margin-top: 89px; */
    /*display: flex;*/
    /*justify-content: center;*/
    height: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    align-items: center;
}

.contentBlock {
    height: 100%;
}

.stepper, .photoWrapBlock, .stepCount, .wrapList {
    min-height: 300px;
}


.text {

    text-align: center;
    font-family: var(--ff-madium);
    font-style: normal;
    font-weight: 500;
    font-size: 40px;
    line-height: 110%;
    letter-spacing: -0.02em;
    justify-content: space-between;
    color: #25495C;

}

.Swiper {
    padding-top: 80px;
}

.mySwiper {
    /*height: 100% !important;*/
    min-height: 350px;
}

.step {
    padding: 3px 7px 4px 7px;
