.wrapperM {
    display: none;
}

.photoWrap {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 173px;
    height: 173px;
    background: #FFFFFF;
    box-shadow: 0 14px 24px rgba(207, 201, 201, 0.25);
    border-radius: 50%;
    margin-right: 140px;
}

.listItem {
    display: flex;
    width: 250px;
    font-family: var(--ff-regular);
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 130%;
    margin-bottom: 25px;
    letter-spacing: -0.02em;
    position: relative;
    color: #607483;

}

.line {
    margin-top: 7px;
    margin-right: 18px;
    min-width: 24px;
    height: 0px;
    border-top: 2px solid #F84263;
    display: inline-block;

}

.stepper {
    margin-right: 83px;
}

.controlWrap {
    position: absolute;
    bottom: 150px;

    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #fff;
    padding: 4px;
    border-radius: 5px;
    width: 95px;
    z-index: 10;

}

.blullfuck {
    width: 100px;
    height: 100px;
    background-color: red;
}

.arr {
    align-self: center;
    z-index: 1000;
    width: 25px;
    height: 20px;
    cursor: pointer;

}

.arrowNextGray {
    width: 25px;
    height: 20px;
    display: inline-block;
    background-image: url('../assets/img/ArrowNextGray.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
}

.arrowPrevGray {
    width: 25px;
    height: 20px;
    display: inline-block;
    background-image: url('../assets/img/ArrowPrevGray.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
}

.pagination {
    text-align: center;
    margin: 0 !important;
    display: inline-block;
    width: 33px;
    font-family: var(--ff-regular);
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 130%;
    letter-spacing: -0.1em;
    color: #607483;
}


