.btn {
    cursor: pointer;
   
    background: #FFFFFF;
    border-radius: 100px;
    font-family: 'GothamProRegular';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 15px;
    text-align: center;
    letter-spacing: -0.02em;
    color: #607483;
    transition: all .35s ease-in-out;
    height: 50px;
    padding:17px 24px;
}

.btnSelected {
    padding:17px 24px;
    height: 50px;
    /*line-height: 15px;*/
    background: #607483 !important;
    -webkit-box-shadow: 0 14px 24px rgba(207, 201, 201, 0.25) !important;
    /*box-shadow: 0 14px 24px rgba(207, 201, 201, 0.25) !important;;*/
    box-shadow: none;
    /*border-radius: 100px !important;;*/
    color: #FFFFFF !important;
}

@media (max-width:768px) {
    .btn {
        cursor: pointer;
        background: #FFFFFF;
        border-radius: 100px;
        font-family: 'GothamProRegular';
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        line-height: 15px;
        text-align: center;
        letter-spacing: -0.02em;
        color: #607483;
        transition: all .35s ease-in-out;
        height: 40px;
        padding:10px 20px;
    }
    
    .btnSelected {
        padding:10px 20px;
        height: 40px;
        /*line-height: 15px;*/
        background: #607483 !important;
        -webkit-box-shadow: 0 14px 24px rgba(207, 201, 201, 0.25) !important;
        /*box-shadow: 0 14px 24px rgba(207, 201, 201, 0.25) !important;;*/
        box-shadow: none;
        /*border-radius: 100px !important;;*/
        color: #FFFFFF !important;
    }
}